<template>
  <div id="user-collapse-component">
    <el-collapse-transition>
      <el-card class="card" v-show="isShow">
        <div class="item" @click="onProfileClick">
          <p>Profile</p>
        </div>
        <div class="item border-bottom" @click="onChangePasswordClick">
          <p>Change Password</p>
        </div>
        <div class="item" @click="onLogOutClick">
          <p>Log Out</p>
        </div>
      </el-card>
    </el-collapse-transition>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref } from "vue";
import { useRouter } from "vue-router";
import { ElNotification } from "element-plus";
import { removeRefreshToken, removeToken } from "@/utils/tokenStorage";

const Component = defineComponent({
  name: "UserCollapse",
  setup() {
    const router = useRouter();

    const isShow = ref(false);

    const toggle = () => {
      isShow.value = !isShow.value;
    };

    const close = () => {
      isShow.value = false;
    };

    const onProfileClick = async () => {
      isShow.value = false;
      await router.push({ name: "UserProfile" });
    };

    const onChangePasswordClick = async () => {
      isShow.value = false;
      await router.push({ name: "ChangePassword" });
    };

    const onLogOutClick = async () => {
      isShow.value = false;
      removeToken();
      removeRefreshToken();
      ElNotification({ type: "success", message: "Log Out Success!" });
      await router.push({ name: "Login" });
    };

    return {
      isShow,

      toggle,
      close,
      onProfileClick,
      onChangePasswordClick,
      onLogOutClick
    };
  }
});

export default Component;
</script>

