import { Module } from "vuex";
import {
  GetFabrics,
  GetFabric,
  GetFabricFilters,
  SetFabricList,
  SetFabricDetail,
  SetFabricFilters,
  PostFabricStatus,
  GetFabricRelates,
  SetFabricRelates,
  PostAddFabricToFavorite
} from "@/typings/store";
import {
  getFabricFilters,
  getFabrics,
  getFabric,
  postFabricStatus,
  getFabricRelates,
  postAddFabricToFavorite
} from "@/apis/fabric";
import { FabricFiltersResponse, FabricResult } from "@/typings/fabric";

type FabricState = {
  list: FabricResult[];
  filters: FabricFiltersResponse;
  detail: {} | null;
  relates: [];
};

const initState = (): FabricState => ({
  list: [],
  filters: {
    materials: [],
    colors: []
  },
  detail: null,
  relates: []
});

export const fabric: Module<FabricState, any> = {
  state: initState(),
  mutations: {
    [SetFabricList](state, data) {
      state.list = data;
    },
    [SetFabricFilters](state, data) {
      state.filters = data;
    },
    [SetFabricDetail](state, data) {
      state.detail = data;
    },
    [SetFabricRelates](state, data) {
      state.relates = data;
    }
  },
  actions: {
    [GetFabricFilters]: async ({ commit }) => {
      const { data } = await getFabricFilters();
      commit(SetFabricFilters, data);
    },
    [GetFabrics]: async ({ commit }, payload) => {
      const { data } = await getFabrics(payload);
      commit(SetFabricList, data.results);
      return { page: payload.page || 1, pageCount: data.total_pages };
    },
    [GetFabric]: async ({ commit }, id) => {
      const { data } = await getFabric(id);
      commit(SetFabricDetail, data);
    },
    [PostFabricStatus]: async ({ commit }, payload) => {
      const { data } = await postFabricStatus(payload);
      return data;
    },
    [GetFabricRelates]: async ({ commit }, payload) => {
      const { data } = await getFabricRelates(payload);
      commit(SetFabricRelates, data);
    },
    [PostAddFabricToFavorite]: async (_, id) => {
      const { data } = await postAddFabricToFavorite(id);
      return data;
    }
  }
};
