import { Module } from "vuex";
import { EdmCardResponse } from "@/typings/edm";
import { GetEdms, SetEdms } from "@/typings/store";
import { getEdms } from "@/apis/edm";

type EdmState = {
  list: EdmCardResponse[];
};

const initState = (): EdmState => ({
  list: []
});

export const edm: Module<EdmState, any> = {
  state: initState(),
  mutations: {
    [SetEdms](state, data) {
      state.list = data;
    }
  },
  actions: {
    [GetEdms]: async ({ commit }) => {
      const { data } = await getEdms();
      commit(SetEdms, data);
    }
  }
};
