import { Module } from "vuex";
import { Login } from "@/typings/store";
import { LoginParams, postLogin, postTokenRefresh } from "@/apis/auth";
import { setRefreshToken, setToken } from "@/utils/tokenStorage";

export const auth: Module<{}, any> = {
  state: {},
  mutations: {},
  actions: {
    [Login]: async ({ commit }, payload: LoginParams) => {
      try {
        const { data } = await postLogin(payload);
        setToken(data.access);
        setRefreshToken(data.refresh);
        return true;
      } catch (e) {
        return false;
      }
    }
  }
};
