import request from "@/apis/http";
import { AxiosPromise } from "axios";
import {
  CompaniesParams,
  CompanyCardResponse,
  CompanyResponse
} from "@/typings/company";

export function getCompanies(
  data: CompaniesParams
): AxiosPromise<CompanyCardResponse[]> {
  return request({
    url: "/companies/",
    method: "GET",
    params: data
  });
}

export function getCompany(id: string): AxiosPromise<CompanyResponse> {
  return request({
    url: `/company/${id}/`,
    method: "GET"
  });
}
