
import { defineComponent, ref, Ref } from "vue";

import NotificationCollapse from "@/components/NotificationCollapse.vue";
import UserCollapse from "@/components/UserCollapse.vue";

import { iCollapse } from "@/typings/collapse";
import notificationIcon from "@/assets/images/icons/png/notification.png";
import userIcon from "@/assets/images/icons/png/user.png";
import chatIcon from "@/assets/images/icons/png/chat.png";

const Component = defineComponent({
  name: "Header",
  components: {
    NotificationCollapse,
    UserCollapse
  },
  setup() {
    const notification: Ref<iCollapse | null> = ref(null);
    const user: Ref<iCollapse | null> = ref(null);

    const onNotificationClick = () => {
      if (!notification.value || !user.value) return;
      user.value.close();
      notification.value.toggle();
    };

    const onUserClick = () => {
      if (!notification.value || !user.value) return;
      notification.value.close();
      user.value.toggle();
    };
    const onChatClick = () => {
      alert("chat clicked");
    };

    return {
      notification,
      user,
      notificationIcon,
      userIcon,
      chatIcon,

      onNotificationClick,
      onUserClick,
      onChatClick
    };
  }
});

export default Component;
