<template>
  <div id="notification-collapse-component">
    <el-collapse-transition>
      <el-card class="card" v-show="isShow">
        <div v-for="index in 5" :key="index" class="item" @click="onItemClick">
          <p>New Notification!</p>
        </div>
      </el-card>
    </el-collapse-transition>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref } from "vue";

const Component = defineComponent({
  name: "NotificationCollapse",
  setup() {
    const isShow = ref(false);

    const onItemClick = () => {
      console.log("click");
    };

    const toggle = () => {
      isShow.value = !isShow.value;
    };

    const close = () => {
      isShow.value = false;
    };

    return {
      isShow,

      toggle,
      close,
      onItemClick
    };
  }
});

export default Component;
</script>

